.titleContent {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    color: #ffffff;
    width: 100%;
    text-decoration: underline #e41376;
  }
  
  form {
    display: flex;
    flex-direction: column;
  }
  
  .editNewsInputGroup {
    display: flex;
    flex-direction: column;
  }
  
  form > div > label {
    font-size: 14pt;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    color: #ffffff;
    margin: 1rem 0 0.3rem 0;
  }
  
  .newsEditInput {
    width: 80%;
    height: 3rem;
    background-color: #10131c;
    border-style: none;
    border-radius: 5px;
    color: #ffffff;
  }
  
  .editNewsButton {
    padding: 0.5rem 1rem;
    background-color: #e41376;
    color: #10131c;
    border-radius: 0.25rem;
    border-style: none;
    font-weight: 700;
    width: 10rem;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    text-decoration: none;
    margin-top: 0.5rem;
  }
  .editNewsButton:hover {
    opacity: 0.8;
    cursor: pointer;
  }
  